<template>
  <div class="m-input m-cell" :class="{ 'm-input-has-right-full': hasRightFullHeightSlot }">
    <div class="m-cell-hd">
      <slot name="label">
        <label class="m-cell-label" :style="labelStyles" v-if="title" v-html="title"></label>
      </slot>
    </div>
    <div class="m-cell-bd">
      <input
        :id="`input-${uuid}`"
        class="my-input"
        :style="inputStyle"
        :type="inputType"
        :name="name"
        :placeholder="placeholder"
        v-model="currentValue"
        :autocomplete="autocomplete"
        :autocapitalize="autocapitalize"
        :autocorrect="autocorrect"
        :spellcheck="spellcheck"
        @focus="focusHandler"
        @blur="onBlur"
        ref="input"/>
    </div>
    <div class="m-cell-ft">
      <i
        class="icon-eye"
        :class="{ 'icon-eye-password': passwordVisibility }"
        v-show="passwdInput && (currentValue.length > 0)"
        @click="showOrHidePassword">eye
      </i>
      <slot name="right"></slot>
      <div v-if="hasRightFullHeightSlot" class="input-right-full">
        <slot name="right-full-height"></slot>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'x-input',
  props: {
    title: {
      type: String,
      default: '',
    },
    type: {
      type: String,
      default: 'text',
    },
    placeholder: String,
    value: [String, Number],
    name: String,
    labelWidth: String,
    labelAlign: String,
    labelMarginRight: String,
    autocomplete: {
      type: String,
      default: 'off',
    },
    autocapitalize: {
      type: String,
      default: 'off',
    },
    autocorrect: {
      type: String,
      default: 'off',
    },
    spellcheck: {
      type: String,
      default: 'false',
    },
  },
  computed: {
    labelStyles() {
      const textAlign = this.labelAlign || this.$parent.labelAlign || 'center';
      return {
        textAlign,
        width: this.labelWidth || this.$parent.labelWidth,
        marginRight: this.labelMarginRight || this.$parent.labelMarginRight,
        paddingLeft: textAlign === 'left' ? '.12rem' : 0,
      };
    },
    inputType() {
      if (!this.passwdInput) return this.type;
      return this.passwordVisibility ? 'text' : 'password';
    },
    passwdInput() {
      return this.type === 'password';
    },
  },
  data() {
    return {
      uuid: Math.random()
        .toString(36)
        .substring(3, 8),
      inputStyle: '',
      currentValue: '',
      passwordVisibility: false,
      hasRightFullHeightSlot: false,
    };
  },
  methods: {
    scrollIntoView(time = 0) {
      setTimeout(() => {
        if (document.activeElement.tagName === 'INPUT' || document.activeElement.tagName === 'TEXTAREA') {
          setTimeout(() => {
            document.activeElement.scrollIntoViewIfNeeded();
          }, time);
        }
      });
    },
    showOrHidePassword() {
      this.passwordVisibility = !this.passwordVisibility;
    },
    focus() {
      this.$refs.input.focus();
    },
    focusHandler($event) {
      this.$emit('on-focus', this.currentValue, $event);
      this.scrollIntoView(500);
    },
    onBlur($event) {
      this.$emit('on-blur', this.currentValue, $event);
      this.inputBlur = true;// 记录失效状态
    },
    // 处理密码输入框再次获焦的删除操作
    handlePasswdInput(newVal, oldVal) {
      if (this.passwdInput && (newVal.toString().length < oldVal.toString().length)) {
        // 删除已输入的密码
        this.$nextTick(() => {
          this.currentValue = '';
        });
      }
    },
  },
  watch: {
    value(newVal) {
      this.currentValue = newVal;
    },
    currentValue(newVal, oldVal) {
      // 更新组件上的v-model
      this.$emit('input', newVal);
      this.$emit('on-change', newVal);
      if (this.inputBlur) {
        this.inputBlur = false;
        this.handlePasswdInput(newVal, oldVal);
      }
    },
  },
  created() {
    this.currentValue = (this.value === undefined || this.value === null) ? '' : this.value;
  },
  beforeMount() {
    if (this.$slots && this.$slots['right-full-height']) {
      this.hasRightFullHeightSlot = true;
    }
  },
};
</script>

<style lang="less" rel="stylesheet/less">
  @import "../../styles/cell";

  @cellLineHeight: .44rem;

  .m-input-has-right-full {
    padding: 0;
  }

  .my-input {
    display: inline-block;
    width: 100%;
    border: 0;
    outline: 0;
    -webkit-appearance: none;
    background-color: transparent;
    font-size: .14rem;
    color: inherit;
    height: @cellLineHeight;
    padding: 0.1rem 0;

    // hides the spin-button
    &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  .input-right-full {
    margin-left: 5px;
    height: @cellLineHeight;
    vertical-align: middle;
    & img {
      height: @cellLineHeight;
    }
  }

  .icon-eye {
    display: inline-block;
    position: relative;
    top: .02rem;
    line-height: 1;
    color: transparent;
    margin-right: .1rem;
    &:after {
      content: " ";
      position: absolute;
      top: 0;
      left: 0;
      width: .17rem;
      height: .12rem;
      background: url(data:image/png;base64,) no-repeat;
      background-size: contain;
    }
  }

  .icon-eye-password::after {
    background-image: url(data:image/png;base64,);
  }
</style>



